.player {
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.time-control {
  width: 50%;
  display: flex;
  align-items: center;
  input {
    width: 100%;
    -webkit-appearance: none;
    background: transparent;
    cursor: pointer;
  }
  p {
    padding: 1rem;
  }
}
.play-control {
  display: flex;
  justify-content: space-evenly;
  // align-items: center;
  padding: 1rem;
  width: 100%;

  svg {
    cursor: pointer;
  }
}
input[type="range"]:focus {
  outline: none;
}
input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 16px;
  width: 16px;
}
input[type="range"]::-moz-slider-thumb {
  -webkit-appearance: none;
  background: transparent;
  border: none;
}
.track {
  background: lightblue;
  width: 100%;
  height: 1rem;
  position: relative;
  border-radius: 1rem;
  overflow: hidden;
}
.animate-track {
  background: rgb(204, 204, 204);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(0%);
  pointer-events: none;
}
.audio-controls {
  display: flex;
  padding: 2rem;
  width: 100%;
  align-items: center;
  flex-direction: column;
}
.volume-slider {
  width: 100%;
  -webkit-appearance: none;
  background: transparent;
  border-radius: 1rem;
  cursor: pointer;
}
@media screen and (max-width: 768px) {
  .time-control {
    width: 90%;
  }
  .play-control {
    // width: 50%;
    justify-content: space-around;
    align-items: center;
  }
}
